.content-wrap{

    .articles_du_blog{
		h2{
			text-align: center;
			color: #000000;
		}

		.articles{
			background-color: #ffffff;
			@include cdm-grid(3, ".article", 30px, 30px, ());

			.article{
				.description{
					text-align: center;
				}
			}
		}
	}

	.content{
		.col-wrapper{
			display: flex;

			.left-col{
				width: 70%;
				max-width: 800px;
			}
			.right-col{
				width: 30%;
				flex: 1 1 auto;
			}
		}

		.articles{
			.article{
				//display: flex;

				figure{
					a{
						opacity: 1;
						@include hover-transition;

						&:hover{
							opacity: 0.6;
						}
					}
				}

				.description{
					//max-width: 50%;
					padding: 15px 20px;

					h3{
						font-weight: 700;
						color: #000000;
						text-transform: uppercase;

						a{
							color: inherit;

							&:hover{
								color: $color_pink;
							}
						}
					}
					.blog-categories{
						a{
							color: $color_pink;
							font-size: 1.6rem;
							@include hover-transition;

							&:hover{
								color: $color_blue;
							}
						}
					}
					p{
						font-size: 1.8rem;
						line-height: 1.3;
					}
				}

				&:nth-child(2n){
					figure{
						order: 1;
					}
					.description{
						padding-left: 0;
						padding-right: 15px;
						order: 0;
					}
				}
			}
		}
	}
}

.page-blog{
	.content-wrap{
		.content{
			.articles{
				.article{
					display: flex;

					.description{
						max-width: 50%;
					}
				}
			}
		}
	}
}